// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { Link } from 'react-router-dom'

// ** Store & Actions
import { getClient } from '../store'
import { useSelector, useDispatch } from 'react-redux'

// ** Reactstrap Imports
import { Row, Col, Alert } from 'reactstrap'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** User View Components
import UserTabs from './Tabs'
import UserInfoCard from './UserInfoCard'

// ** Styles
import '@styles/react/apps/app-users.scss'

const ClientView = () => {
  // ** Store Vars
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const store = useSelector(state => state.client)
  const dispatch = useDispatch()

  // ** Hooks
  const id = localStorage.getItem('clientId')

  // ** Get suer on mount
  useEffect(async () => {
    if (id !== null) {
      setBlock(true)
      await dispatch(getClient(id))
      setBlock(false)
    }
  }, [dispatch])

  const [active, setActive] = useState('contactInformation')

  const toggleTab = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  return store.selectedClient !== null && store.selectedClient !== undefined ? (
    <UILoader blocking={block} loader={<Loader />}>
      <div className='app-user-view'>
        <Row>
          <Col xl='12' lg='12' xs={{ order: 0 }} md={{ order: 0, size: 5 }} sm={{ order: 0 }}>
            <UserInfoCard selectedClient={store.selectedClient} />
          </Col>
          <Col xl='12' lg='12' xs={{ order: 1 }} md={{ order: 1, size: 7 }} sm={{ order: 1 }}>
            <UserTabs active={active} toggleTab={toggleTab} selectedClient={store.selectedClient} />
          </Col>
        </Row>
      </div>
    </UILoader>
  ) : (
    <UILoader blocking={block} loader={<Loader />}>
      <Alert color='danger'>
        <h4 className='alert-heading'>Client not found</h4>
        <div className='alert-body'>
          Client with id: {id} doesn't exist. Check list of all Clients: <Link to='/clients/list'>Clients List</Link>
        </div>
      </Alert>
    </UILoader>
  )
}
export default ClientView
