// ** React Imports
import { Fragment } from 'react'

// ** Reactstrap Imports
import { Card, CardBody, Nav, NavItem, NavLink, TabContent, TabPane } from 'reactstrap'

// ** Icons Imports
import { List } from 'react-feather'


import ConatctInfo from './ConatctInfo'
const UserTabs = ({ active, toggleTab }) => {
  return (
    <Fragment>
      <Nav pills className="flex-lg-row flex-column mb-2 nav nav-left">
        <NavItem>
          <NavLink active={active === 'contactInformation'} onClick={() => toggleTab('contactInformation')}>
            <List className='font-medium-3 me-50' />
            <span className='fw-bold'>Contacts</span>
          </NavLink>
        </NavItem>       
      </Nav>
      <Card>
        <CardBody className='p-1'>
          <TabContent activeTab={active}>
            <TabPane tabId='contactInformation'>
              <ConatctInfo tabId={active} />
            </TabPane>         
          </TabContent>
        </CardBody>
      </Card>
    </Fragment>
  )
}
export default UserTabs
